@import "main-bx";

.model {
    padding: 70px 0;
    &__container {
        display: flex;

        &_reverse {
            flex-direction: row-reverse;
            .model__slider-dots {
                justify-content: flex-end;
            }
        }
    }
    &__title {
        margin: 0;
        margin-bottom: 40px;
        &_mobile {
            display: none;
        }
    }
    &__item {
        &:first-child {
            width: 60%;
            padding-right: 67px;
        }
        &_reverse {
            &:first-child {
                padding-right: 0 ;
            }
            padding-left: 67px;
        }
        &:last-child {
            width: 40%;
        }
        &-top,
        &-bottom {
            width: 100%;
            max-width: 812px;
            
            display: flex;
            justify-content: space-between;
            margin: 0 0 0 auto;
            &_reverse {
                margin: 0 auto 0 0;
            }
        }
        &-bottom {
            justify-content: center;
        }
        &-btn {
            width: 250px;
            height: 52px;
            margin: 0 12px;
        }
        &-list {
            width: 28%;
            padding-right: 10px;
            &:not(:last-child) {
                border-right: 1px solid #4D4D4D;
            }
            p {
                font-family: $font_accent;
                font-weight: bold;
                font-size: 26px;
                line-height: 32px;
                color: $color_white;
                margin-bottom: 30px;
            }

            li {
                font-family: $font_accent;
                font-weight: normal;
                font-size: 18px;
                line-height: 22px;
                color: $color_white;
                padding-bottom: 15px;
            }
        }
        &-line {
            margin: 30px 0;
            width: 100%;
            height: 1px;
            background-color: #4D4D4D;
        }

        &-text {
            padding: 120px 0;
            font-family: $font_accent;
            font-weight: normal;
            font-size: 20px;
            line-height: 24px;
            color: $color_white;
            width: 100%;
            text-align: center;
        }
    }

    &__slider {
        display: flex;
        &-item {
            width: 100%;
            height: 100%;
            display: none;
            .slick-active {
                display: block;
            }
            img {
                width: 100%;
                height: auto;
                object-fit: contain;
            }
        }

        &-dots {
            display: flex;
            align-items: center;
            margin-top: 10px;
            li {
                width: 80px;
                height: 3px;
                background: $color_grey;
                &.slick-active {
                    background-color: $color_firebrick;
                }
                &:not(:last-child) {
                    margin-right: 5px;
                }
            }
            button {
                font-size: 0;
            }
        }
    }
}

@include m-laptop-wide() {
    .model {
        &__item {
            &-top,
            &-bottom {
                max-width: 700px;
            }
        }
    }   
}

@include m-tablet-wide() {
    .model {
        &__item {
            &-top {
                padding-left: 50px;
                &_reverse {
                    padding-left: 0;
                    padding-right: 50px;
                }
            }
            &-top,
            &-bottom {
                max-width: 100%;
            }
            &-list {
                width: auto;
                padding: 0 10px;
            }
        }
    } 
}


@media (max-width: 1170px) {
    .model {
        padding: 30px 0;
        &__container {
            flex-direction: column-reverse;
            width: 100%;
            max-width: map-get($container, laptop);
            margin: 0 auto;

            .model__slider-dots {
                justify-content: center;
            }
        }
        &__item {
            &:last-child,
            &:first-child {
                width: 100%;
                padding: 0;
            }


            &-top {
                padding-left: 0;
                &_reverse {
                    padding-right: 0;
                }
            }
            &-list {
                width: 32%;
            }
        }

        &__slider {
            &-item {
                height: 250px;
            }
        }
    }
}

@include m-phone() {
    .model {
        padding: 15px 0 30px;
        &__title {
            display: none;
            &_mobile {
                display: block;
                border-bottom: 1px solid #4D4D4D;
                padding-bottom: 20px;
                margin-bottom: 20px;
            }
        }
        &__item {
            &:first-child {
                padding: 0 20px;
                
            }
            &-text {
                margin-top: -20px;
                padding: 20px 0;
                border-bottom: 1px solid #4D4D4D;
                text-align: left;
                p {
                    margin: 0;
                }
            }
            &-top {
                flex-direction: column-reverse;
            }
            &-list {
                width: 100%;
                padding: 0;
                &:not(:last-child) {
                    border-right: none;
                }
                border-bottom: 1px solid #4D4D4D;
            }

            &-line {
                display: none;
            }
            &-bottom {
                margin-top: 20px;
            }
        }
    }
}

@media (max-width: 575px) {
    .model {
        &__item {
            &-bottom {
                flex-direction: column-reverse;
            }
            &-btn {
                width: 100%;
                height: 52px;
                margin: 0;
                margin-bottom: 10px;
            }
        }
        &__slider {
            &-dots {
                padding: 0 20px;
                justify-content: space-between;
                li {
                    width: 100%;
                }
            }
        }
    }
}