@import "main-bx";

*,*::after,*::before {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, ul, p {
    margin: 0;
    padding: 0;
}

.container {
    width: 100%;
    max-width: map-get($container, desktop_lg);
    margin: 0 auto;
}

body {
    padding: 0;
    font-family: $font_accent;
    background-color: $color_black;
    line-height: 1;
    width: 100%;
    max-width: $body_width;
    margin: 0 auto !important;
}

a {
    text-decoration: none;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

fieldset,
input,
button {
    padding: 0;
    margin: 0;
    border: none;
    background: transparent;
}
:focus {
    outline-style: none;
    outline-width: 0 !important;
    outline-color: none !important;
}
.wrapper {
    overflow: hidden;
}
@include m-laptop() {
    .container {
        max-width: map-get($container, desktop);
    }
}

@media (max-width: 1170px) {
    .container {
        max-width: map-get($container, laptop);
    }
}
@include m-tablet() {
    .container {
        max-width: map-get($container, tablet);
    }
}

@include m-phone() {
    .container {
        max-width: 100%;
        padding: 0 20px
    }
}