@import "main-bx";

.map {
    padding: 60px 0;
    background-color: $color_imdark;
    &__wrap {
        width: 53%;
        height: 500px;
    }
    &__logo {
        margin-bottom: 50px;
        svg {
            width: 110px;
            height: 60px;
            margin-bottom: 10px;
        }

        &-text {
            font-family: $font_section_titleEx;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: $color_white;
            margin-bottom: 30px;
        }
    }

    &__container {
        display: flex;
        justify-content: space-between;
    }
    &__right {
        width: 42%;
    }
    &__line {
        &_mobile {
            display: none;
            margin-top: 20px;
            transform: translateX(-1050px);
        }
    }

    &__contact {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        svg {
            margin-right: 10px;
            width: 20px;
            height: 20px;
        }
        &-link {
            display: flex;
            align-items: center;

            font-family: $font_section_titleEx;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: $color_white;
            margin-bottom: 7px;
            &:hover {
                text-decoration: underline;
            }
            
        }
        &-time {
            font-family: $font_accent;
            font-weight: normal;
            font-size: 14px;
            line-height: 23px;
            color: $color_grey;
            margin-bottom: 5px;
        }
        
    }



    &__btn {
        display: block;
        padding: 17px 44px;
        margin-bottom: 50px;
    }

    &__social {
        &-wrap {
            display: flex;
            align-items: center;
        }
        &-text {
            font-family: $font_accent;
            font-weight: normal;
            font-size: 18px;
            line-height: 22px;
            color: $color_white;
            margin-bottom: 30px;
        }
        &-icon {
            width: 26px;
            height: 26px;
            margin-right: 25px;
            &:hover svg {
                fill: $color_grey;
            }
        }
        svg {
            width: 26px;
            height: 26px;
            fill: $color_white;
            transition: $trns;
        }
    }
}

@include m-tablet() {
    .map {
        padding: 40px 0;
        &__container {
            flex-direction: column;
        }
        &__wrap {
            height: 375px;
        }
        &__right,
        &__wrap {
            width: 100%;
        }
        &__right {
            padding: 0 20px;
        }
        &__line {
            display: none;
            &_mobile {
                display: block;
            }
        }
    }
}

@media (max-width: 500px) {
    .map {
        &__btn {
            width: 100%;
            padding: 17px 0;
        }
        &__logo {
            margin-bottom: 15px;
            svg {
                width: 80px;
                height: 30px;
            }
            &-text {
                margin-bottom: 0;
            }
        }
        &__social {
            &-icon {
                width: 52px;
                height: 52px;
                &:not(:last-child) {
                    margin-right: 10px;
                }
            }
        }
    }
}