@import "main-bx";

.request {
    &.fancybox-content {
        background-color: $color_imdark;
    }
    &__inner {
        width: 100%;
        max-width: 920px;
        height: 566px;
        padding: 80px 180px;
        .form__title {
            margin-bottom: 30px;
        }
        .form__text {
            margin-bottom: 50px;
        }
        .form__title,
        .form__text {
            text-align: center;
        }
        .form__form {
            margin: 0 auto;
        }
    }

    & .fancybox-button {
        right: 17px !important;
        top: 7px !important;
        svg {
            width: 40px;
            height: 40px;
            opacity: 0.6;
        }
    }
}

@include m-phone() {
    .request {
        &__inner {
            max-width: 100%;
            padding: 40px 20px;
            background-color: $color_imdark;
        }
        &.fancybox-content {
            padding: 0 20px;
            background-color: transparent;
        }

        & .fancybox-button {
            right: 35px !important;
            top: 7px !important;
        }
    }
}