@import "main-bx";
.btn {
    font-family: $font_accent;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    color: $color_white;
    cursor: pointer;
    transition: $trns;

    @include m-phone() {
        font-size: 12px;
        line-height: 23px;
    }
    &_skew {
        padding: 16px 55px;
        position: relative;
        display: inline-block;
        
        @include m-laptop() {
            padding: 16px 30px;
            
        }
        @media (max-width: 1170px) {
            padding: 16px 19px;
        }
        &:hover::before {
            background-color: $color_indian_red;
        }
        &::before {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            content: "";
            background-color: $color_firebrick;
            width: 100%;
            height: 100%;
            transform: skew(-28deg);
        }
    }

    &_tsp {
        border: 1px solid $color_white;
        &:hover {
            color: $color_foggy;
            border-color: $color_foggy;
            background-color: $color_imdark;
        }
    }
    &_white {
        color: $color_black;
        background-color: $color_white;
        border: 1px solid $color_white;
        &:hover {
            border-color: $color_foggy;
            background-color: $color_foggy;
        }
    }
}