@import "main-bx";
.anchor {
    padding: 50px 0;
    &__title {
        text-align: center;
        margin-bottom: 60px;
        span {
            position: relative;
            display: inline-block;
            &::before {
                content: "";
                position: absolute;
                bottom: 5px;
                left: 0;
                z-index: -1;
                width: 30px;
                height: 30px;
                background-color: $color_firebrick;
                transform: skew(-20deg);
            }
        }
    }
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 80px;
        padding: 0 0 0 13px;

        .btn {
            &:not(:last-child) {
                margin-right: 13px;
            }
        }
    }
    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 18px 0;
        border-bottom: 1px solid #222222;

        a {
            font-family: $font_accent;
            font-weight: normal;
            font-size: 18px;
            line-height: 22px;

            color: $color_grey;

            transition: $trns;

            &:hover {
                color: $color_white;
            }
        }
    }

    &__links {
        &_fidex {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            padding: 0 100px;
            background-color: rgba(0,0,0, .8);
            z-index: 1000;
        }
    }
}
@include m-tablet() {
    .anchor {
        &__top {
            flex-wrap: wrap;
            justify-content: flex-start;
            .btn {
                margin-bottom: 10px;
            }
        }
        &__bottom {
            flex-wrap: wrap;
            justify-content: flex-start;
            a {
                margin: 0 25px 25px 0;
            }
        }
    }
}

@include m-phone() {
    .anchor {
        padding: 30px 0;
        &__top {
            margin-bottom: 40px;
            .btn_sec {
                order: 5;
            }
        }
        &__bottom {
            font-size: 14px;
            line-height: 23px;
            padding: 0;
            border: none;
        }

        &__links {
            &_fidex {
                padding: 15px 20px;
                .anchor__bottom a {
                    color: $color_white;
                }
            }
        }
    }
}

@media (max-width:650px) {
    .anchor {
        &__top {
    
            .btn {
                &:not(:last-child) {
                    margin-right: 0;
                }
                margin-right: 10px !important;
            }
        }
    }
}

@media (max-width: 500px) {
    .anchor {
        &__top {
            justify-content: space-between;
            .btn {
                margin-right: 0 !important;
                padding: 14px 0;
                text-align: center;
            }
            .btn_frs {
                width: 48.5%;
            }

            .btn_sec {
                width: 100%;
            }
        }
    }
}


@media (max-width: 360px) {
    .anchor {
        &__top {
            .btn {
                font-size: 10px;
            }
        }
    }
}