@import "main-bx";

.header {
    padding: 30px 0;
    background-color: $color_black;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
	&__logo {
        &_mobile {
            display: none;
        }
        a {
            display: flex;
            align-items: center;
            
        }
        svg {
            width: 88px;
            height: 30px;
            margin-right: 22px;
        }
        &-right {
            font-family: $font_section_titleEx;
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
        }
        &-name {
            display: block;
            color: $color_firebrick;
            margin-bottom: 7px;
        }
        &-text {
            display: block;
            color: $color_white;
        } 
    }

    &__right {
        display: flex;
        align-items: center;
        svg {
            width: 20px;
            height: 20px;
        }
        
    }
    &__contacts {
        display: flex;
        flex-direction: column;
        margin-right: 50px;
        &-link {
            display: flex;
            align-items: center;

            font-family: $font_accent;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: $color_white;
            margin-bottom: 7px;
            &:hover {
                text-decoration: underline;
            }
            svg {
                margin-right: 10px;
            }
        }
        &-btn {
            margin-left: 50px;
            .btn {
                padding: 10px 15px;
            }
        }
    }

    &__address {
        &-item {
            display: flex;
            align-items: center;

            font-family: $font_accent;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: $color_white;
            svg {
                margin-right: 10px;
            }
            &:not(:last-child) {
                margin-bottom: 7px;
            }
        }
    }
}

@include m-tablet-wide(){
    .header {
        &__contacts {
            margin-right: 50px;
        }
    }
}

@include m-tablet() {
    .header {
        
        &__inner {
            position: relative;
        }
        &__logo {
            svg {
                display: none;
            }
            &_mobile {
                display: block;
                svg {
                    width: 80px;
                    height: 30px;
                    display: block;
                }
            }
        }
        &__contacts {
            margin-right: 20px;
            &-link {
                font-size: 0;
                margin-bottom: 0;
                svg {
                    margin-right: 0;
                }
            }
            &-btn {
                display: none;
            }
        }
        &__address {
            display: none;
        }
        
    }
}


@include m-phone() {
    .header {
        padding: 15px 0;

        &__logo {
            &-right {
                font-size: 14px;
                line-height: 17px;
            }
        }
    }
}
