@import "main-bx";

.form {
    &_bg {
        background-image: url('../img/content/bg2.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
    }

    &__inner {
        padding: 75px 0; 
        
    }
    &__img {
        display: none;
    }
    &__title {
        text-align: left;
        margin-top: 0;
        margin-bottom: 60px;
    }
    &__text {
        font-family: $font_accent;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: $color_foggy;
    }
    &__form {
        width: 100%;
        max-width: 400px;
        fieldset {
            padding: 0;
            margin: 0;
            background: transparent;
            border: none;
        }
        .btn {
            padding: 17px 0;
            width: 100%;
        }
    }
    &__input {
        margin-bottom: 40px;
        border-bottom: 1px solid #B2B2B2;
        padding-bottom: 7px;
        &:focus {
            border-bottom: 1px solid $color_white;
        }
        
        input {
            font-family: $font_accent;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: $color_grey;
            &:focus {
                color: $color_white;
            }
        }
    }
    &__checkbox {
        margin-top: 30px;
        input {
            display: none;
            &:checked + .form__checkbox-box svg {
                transform: scale(1);
            }
        }
        &-box {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            width: 24px;
            height: 24px;
            border: 1px solid $color_grey;

            svg {
                width: 12px;
                height: 12px;
                transform: scale(0);
                transition: $trns;
            }
        }
        &-text {
            font-family: $font_accent;
            font-weight: normal;
            font-size: 12px;
            line-height: 15px;
            color: $color_grey;
            margin-left: 10px;
        }
    }
}

@include m-phone() {
    .form {
        &_bg {
            background: none;
        }
        &__img {
            display: block;
            width: 100%;
            height: auto;
            img {
                width: 100%;
                height: auto;
                object-fit: cover;
                margin-bottom: 20px;
            }
        }
        &__inner {
            
            padding: 30px 0; 
        }
        &__form {
            max-width: 100%;
        }
    }
}